.tb_nc_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.tb_nc_post_wrapper{
    width: 25%;
    padding: 7.5px;
}
.tb_nc_post_wrap_in {
    border:1px solid rgba(0, 0, 0, 0.1);
    overflow: hidden;
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
}

.tb_nc_post_in{
    display: block;
    position: relative;
    cursor: pointer;
}

.tb_nc_media_wrap{
    position: relative;
    display: block;
    width: 100%;
}

.tb_nc_image {
    background-repeat: no-repeat;
    background-size: contain !important;
    background-position: center;
}

.tb_nc_image_wrap_{
    padding-bottom: 100%;
    position: relative;
}

.tb_nc_image, .tb_nc_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_nc_video{
    background-color: #000;
}

.tb_nc_video video,
.tb_nc_video iframe{
    object-fit: cover;
    object-position: center;
}

.tb_nc_video > div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_nc_contant_wrapper {
    padding: 10px;
    font-family: Inter, sans-serif;
}

.tb_nc_author {
    width: calc(100% - 30px);
}

.tb_nc_rating__ .tb__rating__{
    padding: 12px 0;
}

.tb_nc_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    background-color: rgba(0,0,0,.2);
    overflow: hidden;
}
.tb_nc_author_profile__{
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_nc_author_top .tb_nc_author_profile{
    position: relative;
    margin-top: -34px;
    z-index: 3;
}

.tb_nc_authorname {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: left !important;
    line-height: normal;
}

.tb_nc_authorname {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
    font-style: var(--tb-font-style);
}

.tb_nc_username,
.tb_nc_time {
    font-family: var(--tb-font);
    color: var(--tb-author-color);
    font-weight: var(--tb-font-weight);
    font-style: var(--tb-font-style);
}

.tb_nc_seprator {
    background-color: var(--tb-author-color);
}

.tb_nc_content,
.tb_nc_rating_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
    font-weight: var(--tb-font-weight);
    font-style: var(--tb-font-style);
}

.tb_nc_social_action__{
    min-height: 33px;
}

.tb_nc_social_action__ .tb_social_action_ico__, 
.tb_nc_social_action__ .tb_social_action_counts__ {
    color: var(--tb-author-color);
}

.tb_nc_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_nc_social_ico{
    font-size: 20px;
    color: var(--tb-icon-color);
}

.tb_nc_social_top_ .tb_nc_social_ico{
    text-shadow: 0 2px 5px rgba(0, 0, 0, 0.35);
    color: #fff !important;
}

.tb_nc_author_wrapper {
    display: flex;
    align-items: center;
}

.tb_nc_author_info {
    overflow: hidden;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    padding-right: 10px;
}

.tb_nc_post_info {
    display: flex;
    align-items: center;
}

.tb_nc_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_nc_username, .tb_nc_time {
    font-size: 10px;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_nc_username{
    max-width: 65%;
}

.tb_nc_social_top_{
    position: absolute;
    z-index: 4;
    right: 10px;
    top: 10px;
}

.tb_nc_time{
    max-width: 35%;
    min-width: 70px;
}

.tb_nc_social_ {
    font-size: 24px;
}

.tb_nc_media_icon_te.tb__media_ico_ .tb__icon:nth-child(1){
    right: auto;
    left: var(--tb-icon-position);
    top: var(--tb-icon-position);
}
.tb_nc_media_icon_te.tb__media_ico_ .tb__icon:nth-child(2){
    right: auto;
    top:auto;
    left: var(--tb-icon-position);
    bottom: var(--tb-icon-position);
}

.tb_nc_post_in:hover .tb_share_wrapper{
    opacity: 1;
    visibility: visible;
}


.tb_nc_content, .tb_nc_rating_content {
    margin: 10px 0;
    word-break: break-word;
    white-space: pre-line;
}

.tb_nc_share_container.tb_share_wrapper{
    z-index: 4;
}

.tb_nc_content{
    text-align: left;
}

.tb_nc_content .tb_text_links_, .tb_nc_rating_content .tb_text_links_ {
    color: inherit !important;
    text-decoration: underline !important;
    display: inline;
}

.tb_nc_rating_content, .tb_nc_text_post {
    text-align: center;
}

.tb_nc_text_post{
    margin-top: 30px;
    margin-bottom: 20px;
}

.tb_nc_text_decoration::before{
    content: "\e909";
    font-family: 'WebSocialFonts';
    margin-right: 12px;
}

.tb_nc_text_decoration::after{
    content: "\e90a";
    font-family: 'WebSocialFonts';
    margin-left: 12px;
}

.tb_nc_post_in:hover .tb__media_ico_ .tb__icon,
.tb_nc_post_in:hover .tb__media_ico_ .tb__icon{
    opacity: 0;
    transform: scaleX(.4) scaleY(.4) translateZ(0);
}

.tb_nc_video_ico, .tb_nc_audio_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    z-index: 1;
    color: #fff;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}
.tb_nc_video_ico{
    font-size: 12px;
}
.tb_nc_audio_ico{
    font-size: 16px;
}

.tb_nc_multi_images_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}

.tb_nc_multi_images_ico_bottom {
    position: absolute;
    right: 12px;
    bottom: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}

.tb_nc_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 34px;
}

.tb_nc_post_cta {
    display: block;
    text-align: center;
    margin-bottom: 10px;
}

.tb_nc_post_cta_btn {
    font-family: var(--tb-font);
    padding: var(--tb-cta-padding);
    color: var(--tb-cta-color);
    background-color:  var(--tb-cta-bg-color);
    border-radius: var(--tb-cta-border-radius);
    font-weight: var(--tb-cta-font-weight);
    border-color: var(--tb-cta-border-color);
    transition: var(--tb-cta-transition);
    min-height: var(--tb-cta-height);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    line-height: normal;
}

.tb_nc_post_cta_btn:hover {
    color: var(--tb-cta-color-hover);
    background-color: var(--tb-cta-bg-color-hover);
    border-color: var(--tb-cta-border-color-hover);
}

.tb_nc_post_gradient .tb_nc_post_cta_btn {
    color: #000;
    background-color: #fff;
    border-radius: var(--tb-btn-border-radius);
    font-weight: var(--tb-btn-font-weight);
    border-color: #fff;
}

.tb_nc_post_gradient .tb_nc_post_cta_btn:hover {
    color: #000;
    background-color: #fff;
    border-color: #fff;
}

.tb_nc_gradient-1 .tb_nc_post_wrap_in{
    background:linear-gradient(-45deg, rgba(254,217,103,1) 0%, rgba(255,255,135,1) 100%);
} 
.tb_nc_gradient-2 .tb_nc_post_wrap_in{
    background:linear-gradient(45deg, rgba(59,220,183,1) 0%, rgba(147,227,142,1) 100%);
}
.tb_nc_gradient-3 .tb_nc_post_wrap_in{
    background:linear-gradient(-45deg, rgba(252,165,128,1) 0%, rgba(255,197,172,1) 100%);
}
.tb_nc_gradient-4 .tb_nc_post_wrap_in{
    background:linear-gradient(45deg, rgba(0,108,239,1) 0%, rgba(122,186,245,1) 100%);
}
.tb_nc_gradient-5 .tb_nc_post_wrap_in{
    background:linear-gradient(-45deg, rgba(131,212,254,1) 0%, rgba(172,249,254,1) 100%);
}  
.tb_nc_gradient-6 .tb_nc_post_wrap_in{
    background:linear-gradient(-45deg, rgba(255,63,116,1) 0%, rgba(255,144,184,1) 100%);
}

.tb_nc_gradient-1 .tb_nc_authorname,
.tb_nc_gradient-1 .tb_nc_username,
.tb_nc_gradient-1 .tb_nc_time,
.tb_nc_gradient-1 .tb_nc_social_ico,
.tb_nc_gradient-1 .tb_nc_rating_content,
.tb_nc_gradient-1 .tb_nc_content,

.tb_nc_gradient-2 .tb_nc_authorname,
.tb_nc_gradient-2 .tb_nc_username,
.tb_nc_gradient-2 .tb_nc_time,
.tb_nc_gradient-2 .tb_nc_content,

.tb_nc_gradient-3 .tb_nc_authorname,
.tb_nc_gradient-3 .tb_nc_username,
.tb_nc_gradient-3 .tb_nc_time,
.tb_nc_gradient-3 .tb_nc_rating_content,
.tb_nc_gradient-3 .tb_nc_content,

.tb_nc_gradient-5 .tb_nc_authorname,
.tb_nc_gradient-5 .tb_nc_username,
.tb_nc_gradient-5 .tb_nc_time,
.tb_nc_gradient-5 .tb_nc_rating_content,
.tb_nc_gradient-5 .tb_nc_content{
    color: #000;
} 

.tb_nc_gradient-1 .tb_nc_seprator,
.tb_nc_gradient-2 .tb_nc_seprator,
.tb_nc_gradient-3 .tb_nc_seprator,
.tb_nc_gradient-5 .tb_nc_seprator{
    background-color: #000;
}

.tb_nc_gradient-4 .tb_nc_authorname,
.tb_nc_gradient-4 .tb_nc_username,
.tb_nc_gradient-4 .tb_nc_time,
.tb_nc_gradient-4 .tb_nc_social_ico,
.tb_nc_gradient-4 .tb_nc_rating_content,
.tb_nc_gradient-4 .tb_nc_content,
.tb_nc_gradient-4 .tb_social_action_ico__, 
.tb_nc_gradient-4 .tb_social_action_counts__,

.tb_nc_gradient-6 .tb_nc_authorname,
.tb_nc_gradient-6 .tb_nc_username,
.tb_nc_gradient-6 .tb_nc_time,
.tb_nc_gradient-6 .tb_nc_social_ico,
.tb_nc_gradient-6 .tb_nc_rating_content,
.tb_nc_gradient-6 .tb_nc_content,
.tb_nc_gradient-6 .tb_social_action_ico__, 
.tb_nc_gradient-6 .tb_social_action_counts__{
    color: #fff;
}

.tb_nc_gradient-4 .tb_nc_seprator,
.tb_nc_gradient-6 .tb_nc_seprator{
    background-color: #fff;
}

.tb_nc_post_wrapper_colors .tb_rating_ico__{
    font-size: 20px;
}

.tb_nc_post_wrapper_colors .tb_share_wrapper {
    left: 18px;
    top: 84px;
}

.tb_nc_gradient-1 .tb_nc_rating__ .tb_rating_ico__:before{
    background:-moz-linear-gradient(45deg, #000 0%, #000 50%, #635A32 80%, #918135 100%); 
    background: -webkit-linear-gradient(45deg, #000 0%, #000 50%, #635A32 80%, #918135 100%); 
    background: linear-gradient(to bottom, #000 0%, #000 50%, #635A32 80%, #918135 100%);
    filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.15));
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color:transparent;
}

